@import "variables";

.header-block {
  header {
    position: relative;
    h2 {
      position: absolute;
      width: fit-content;
      margin: 0;
      text-align: center;
      top: 0em;
      left: 50%;
      transform: translateX(-50%) translateY(-40%);
      padding: 0 1em;
      background-color: white;
      white-space: nowrap;

      color: $black;
      font-size: 2em;
      font-weight: 600;
    }
  }
}

.header-block__body {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 2.5em;
  border: 2px solid $black;
  height: 25em;

  .row {
    display: grid;
    grid-template-columns: 2fr 1fr;
    padding-block: 0.5em;
    p {
      font-size: 1.125em;
      margin: 0;
    }
  }

  .header {
    p {
      color: $black;
    }
  }
  .main {
    overflow-y: scroll;
    overflow-x: hidden;
    flex-grow: 1;
    p {
      color: $primary-300;
    }
  }
  .footer {
    p {
      color: $dark;
    }
    p:first-of-type {
      color: $black;
    }
  }
}

@include sm {
  @import "checkout-order-sm";
}
