:root {
  --fs-1: 3.13em;
  --fs-2: 2.5em;
  --fs-3: 1.56em;
  --fs-4: 1.56em;
  --fs-5: .75em;
  --fs-6: 1em;
  --fs-7: .63em;
}

@media screen and (min-width: 35.5em) {
  :root {
    --fs-1: 6.25em;
    --fs-2: 3.13em;
    --fs-3: 1.94em;
    --fs-4: 1.56em;
    --fs-5: 1.25em;
    --fs-6: .81em;
