.header-block header {
  position: relative;
}

.header-block header h2 {
  text-align: center;
  white-space: nowrap;
  color: #201d1f;
  background-color: #fff;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0;
  padding: 0 1em;
  font-size: 2em;
  font-weight: 600;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%)translateY(-40%);
}

.header-block__body {
  box-sizing: border-box;
  border: 2px solid #201d1f;
  flex-direction: column;
  height: 25em;
  padding: 2.5em;
  display: flex;
}

.header-block__body .row {
  grid-template-columns: 2fr 1fr;
  padding-block: .5em;
  display: grid;
}

.header-block__body .row p {
  margin: 0;
  font-size: 1.125em;
}

.header-block__body .header p {
  color: #201d1f;
}

.header-block__body .main {
  flex-grow: 1;
  overflow-x: hidden;
  overflow-y: scroll;
}

.header-block__body .main p {
  color: #ffb5dd;
}

.header-block__body .footer p {
  color: #000;
}

.header-block__body .footer p:first-of-type {
  color: #201d1f;
}

@media screen and (min-width: 35.5em) {
  .header-block header h2 {
    font-size: 2.5em;
  }

  .header-block__body {
    height: 26em;
    padding: 1.5em;
  }

  .header-block__body .row p {
    font-size: 1.5em;
  }
}

